<template>
  <div class="teacher-class-signIn">
    <div class="teacher-class-header">
      <span class="title">三年(2)班 —— 学会签到详情</span>
      <span class="back-btn" @click="back">返回</span>
    </div>
    <div class="teacher-class-sub-panel">
      <div class="head">
        <span
          class="data"
          :class="{'choosed':isChoosed==0}"
          @click="selectType='date';isChoosed=0"
        >日</span>
        <span
          class="data"
          :class="{'choosed':isChoosed==1}"
          @click="selectType='month';isChoosed=1"
        >月</span>
        <span
          class="data"
          :class="{'choosed':isChoosed==2}"
          @click="selectType='year';isChoosed=2"
        >年</span>
        <el-date-picker
          class="choose-data"
          v-model="selectDate"
          :type="selectType"
          :clearable="false"
        ></el-date-picker>
        <span class="icon">
          <i class="iconfont icon-fangkuai red-text"></i>&nbsp;
          <span class="fr">旷课</span>
        </span>
        <span class="icon">
          <i class="iconfont icon-fangkuai green-text"></i>&nbsp;
          <span class="fr">早退</span>
        </span>
        <span class="icon">
          <i class="iconfont icon-fangkuai grown-text"></i>&nbsp;
          <span class="fr">迟到</span>
        </span>
      </div>
      <div class="chart-box" style="height:460px">
        <div id="signInChart" style="width:100%;height:100%"></div>
      </div>
    </div>
    <div class="rank-panel">
      <div class="head">
        <span class="fl">排行榜</span>
        <span class="picker">
          <span class="iconfont icon-xiangzuo" v-show="!isMin" @click="preMonth"></span>
          <div class="title">
            <span class="year-month">{{yearMonth}}</span>
            <el-date-picker
              v-model="yearMonth"
              :clearable="false"
              type="month"
              value-format="yyyy-MM"
              @change="pickYear"
              class="pick-data"
            ></el-date-picker>
          </div>
          <span class="iconfont icon-jiantou" v-show="!isMax" @click="nextMonth"></span>
        </span>
        <el-tabs class="sub-tabs" v-model="activeName" @tab-click="handleClick">
          <el-tab-pane label="迟到" name="first"></el-tab-pane>
          <el-tab-pane label="早退" name="second"></el-tab-pane>
          <el-tab-pane label="旷课" name="third"></el-tab-pane>
        </el-tabs>
      </div>
      <div class="box">
        <div class="sign-in-item" v-for="(item,index) in signInList" :key="index">
          <div class="img-box">
            <img :src="item.img" :draggable="false">
          </div>
          <div class="detail">
            <div class="name">{{item.name}}</div>
            <div class="class">{{item.class}}</div>
          </div>
          <div class="badges">
            <span class="grown-bg">{{item.earlier}}</span>
            <span class="green-bg">{{item.late}}</span>
            <span class="red-bg">{{item.absenteeism}}</span>
          </div>
        </div>
        <div class="course-page" style="margin-top:58px">
          <el-pagination layout="prev, pager, next" :page-size="6" :total="allItems"></el-pagination>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import "echarts/lib/chart/pie";
let echarts = require("echarts/lib/echarts");
export default {
  data() {
    return {
      isChoosed: 0,
      selectType: "date",
      selectDate: new Date(),
      yearMonth: "2018-11",
      isMin: false,
      isMax: false,
      activeName: "first",
      allItems: 22,
      signInList: [
        {
          img:
            "https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1541394127913&di=0557d44eb41c645b1691a060250da601&imgtype=0&src=http%3A%2F%2Fimg.pconline.com.cn%2Fimages%2Fupload%2Fupc%2Ftx%2Fwallpaper%2F1603%2F31%2Fc2%2F19819788_1459402903342_800x800.jpg",
          name: "周慧琳",
          class: "三年（2）班",
          earlier: 5,
          late: 2,
          absenteeism: 1
        },
        {
          img:
            "https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1541394127913&di=0557d44eb41c645b1691a060250da601&imgtype=0&src=http%3A%2F%2Fimg.pconline.com.cn%2Fimages%2Fupload%2Fupc%2Ftx%2Fwallpaper%2F1603%2F31%2Fc2%2F19819788_1459402903342_800x800.jpg",
          name: "周慧琳",
          class: "三年（2）班",
          earlier: 5,
          late: 2,
          absenteeism: 1
        },
        {
          img:
            "https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1541394127913&di=0557d44eb41c645b1691a060250da601&imgtype=0&src=http%3A%2F%2Fimg.pconline.com.cn%2Fimages%2Fupload%2Fupc%2Ftx%2Fwallpaper%2F1603%2F31%2Fc2%2F19819788_1459402903342_800x800.jpg",
          name: "周慧琳",
          class: "三年（2）班",
          earlier: 5,
          late: 2,
          absenteeism: 1
        },
        {
          img:
            "https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1541394127913&di=0557d44eb41c645b1691a060250da601&imgtype=0&src=http%3A%2F%2Fimg.pconline.com.cn%2Fimages%2Fupload%2Fupc%2Ftx%2Fwallpaper%2F1603%2F31%2Fc2%2F19819788_1459402903342_800x800.jpg",
          name: "周慧琳",
          class: "三年（2）班",
          earlier: 5,
          late: 2,
          absenteeism: 1
        },
        {
          img:
            "https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1541394127913&di=0557d44eb41c645b1691a060250da601&imgtype=0&src=http%3A%2F%2Fimg.pconline.com.cn%2Fimages%2Fupload%2Fupc%2Ftx%2Fwallpaper%2F1603%2F31%2Fc2%2F19819788_1459402903342_800x800.jpg",
          name: "周慧琳",
          class: "三年（2）班",
          earlier: 5,
          late: 2,
          absenteeism: 1
        },
        {
          img:
            "https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1541394127913&di=0557d44eb41c645b1691a060250da601&imgtype=0&src=http%3A%2F%2Fimg.pconline.com.cn%2Fimages%2Fupload%2Fupc%2Ftx%2Fwallpaper%2F1603%2F31%2Fc2%2F19819788_1459402903342_800x800.jpg",
          name: "周慧琳",
          class: "三年（2）班",
          earlier: 5,
          late: 2,
          absenteeism: 1
        }
      ]
    };
  },
  mounted() {
    this.drawSignInChart();
  },
  methods: {
    handleClick() {
      console.dir(this.activeName);
    },
    preMonth() {
      let year = this.yearMonth.split("-")[0];
      let month = this.yearMonth.split("-")[1];
      month--;
      if (month < 1) {
        year--;
        month = 12;
      }
      this.yearMonth = `${year}-${month < 10 ? `0${month}` : month}`;
    },
    nextMonth() {
      let year = this.yearMonth.split("-")[0];
      let month = this.yearMonth.split("-")[1];
      month++;
      if (month > 12) {
        year++;
        month = 1;
      }
      this.yearMonth = `${year}-${month < 10 ? `0${month}` : month}`;
    },
    pickYear() {
      console.dir(this.yearMonth);
    },
    back() {
      this.$router.back();
    },
    drawSignInChart() {
      let option = {
        xAxis: {
          type: "category",
          axisLine: {
            lineStyle: {
              color: "#aaa"
            }
          },
          splitLine: {
            show: true
          },
          data: [8, 9, 10, 11, 12, 13]
        },
        yAxis: {
          type: "value",
          axisLine: {
            lineStyle: {
              color: "#aaa"
            }
          }
        },
        grid: {
          top: "8%",
          bottom: "8%",
          left: "5%",
          right: "2%"
        },
        series: [
          {
            label: {
              show: true,
              position: "top",
              fontSize: 16
            },
            silent: true,
            barGap: ".8",
            barWidth: "20",
            itemStyle: {
              color: "#eeb00a"
            },
            data: [13, 10, 16, 8, 12, 8],
            type: "bar"
          },
          {
            label: {
              show: true,
              position: "top",
              fontSize: 16
            },
            silent: true,
            barWidth: "20",
            itemStyle: {
              color: "#4CD64C"
            },
            data: [8, 9, 5, 6, 10, 5],
            type: "bar"
          },
          {
            label: {
              show: true,
              position: "top",
              fontSize: 16
            },
            silent: true,
            barWidth: "20",
            itemStyle: {
              color: "#F76E6E"
            },
            data: [5, 9, 4, 5, 4, 2],
            type: "bar"
          }
        ]
      };
      let chart = echarts.init(document.getElementById("signInChart"));
      chart.setOption(option);
    }
  }
};
</script>
<style lang="less">
@itemHeight: 66px;
.teacher-class-signIn {
  .rank-panel {
    width: 100%;
    height: 800px;
    background: #fff;
    padding: 0 22px;
    margin-top: 22px;
    .head {
      height: 42px;
      line-height: 42px;
      user-select: none;
      .picker {
        width: 220px;
        padding-top: 5px;
        float: left;
        margin-left: 242.5px;
        display: flex;
        justify-content: space-between;
        color: #aaa;
        .title {
          width: 86px;
          position: relative;
          .year-month,
          .pick-data {
            width: 100% !important;
            position: absolute;
            left: 0;
            top: 0;
          }
          .pick-data {
            opacity: 0;
            &:hover {
              cursor: pointer;
            }
            .el-input__inner {
              width: 100% !important;
              height: 22px !important;
              &:hover {
                cursor: pointer;
              }
            }
          }
        }
        .iconfont:hover {
          cursor: pointer;
        }
      }
      .sub-tabs {
        width: 166px;
        float: right;
        .el-tabs__header {
          margin: 0 !important;
        }
        .el-tabs__nav-wrap::after {
          background-color: transparent !important;
        }
      }
    }
    .box {
      width: 100%;
      margin-top: 22px;
      .sign-in-item {
        width: 100%;
        height: 82px;
        border-bottom: 1px solid #eee;
        margin-bottom: 18px;
        .img-box {
          width: @itemHeight;
          height: @itemHeight;
          float: left;
          border-radius: 50%;
          overflow: hidden;
          img {
            width: 100%;
            height: 100%;
          }
        }
        .detail {
          height: @itemHeight;
          float: left;
          margin-left: 32px;
          text-align: left;
          display: flex;
          flex-direction: column;
          justify-content: space-around;
          .class {
            margin-top: 12px;
            color: #aaa;
            font-size: 0.9rem;
          }
        }
        .badges {
          width: 165px;
          height: @itemHeight;
          float: right;
          display: flex;
          justify-content: space-between;
          align-items: center;
          span {
            border-radius: 50%;
            padding: 0 8px;
            color: #fff;
          }
        }
      }
    }
  }
}
</style>



